import React, { Component, Fragment } from 'react';
import downArrow from '../../assets/images/down-arrow.png';
import downArrowGlow from '../../assets/images/down-arrow-glow.png';
import Scroll from 'react-scroll-to-element';

class Header extends Component {
  constructor() {
    super();
    this.state = { 
      isHidden: true,
      isSlidingOut: false,
    }
  }

  showMenu = () => {
    this.setState({ isHidden: false });
  }

  slideMenuOut = () => {
    this.setState({ isSlidingOut: true });
  }

  hideMenu = () => {
    // Triggers the menu to slide out
    this.slideMenuOut();

    // After the menu slides out, the menu is hidden. The timeout time must match the animation-duration time.
    setTimeout(() => {
      this.setState({ isHidden: true, isSlidingOut: false });
    }, 1000);
  }
    
  render () {
    const { isHidden, isSlidingOut } = this.state;

    return (
      <Fragment>
        <header className="header">
          <Scroll type="id" element="_header">
            <h2 className="name">JEMAINE BROWN</h2>
          </Scroll>

          <nav className="nav-1">
            <Scroll type="id" element="_about" offset={-30}>ABOUT</Scroll>
            <Scroll type="id" element="_whatiuse" offset={-40}>WHAT I USE</Scroll>
            <Scroll type="id" element="_portfolio" offset={-30}>PORTFOLIO</Scroll>
            <Scroll type="id" element="_contact" offset={-30}>CONTACT</Scroll>
            <Scroll type="id" element="_footer">LINKS</Scroll>
          </nav>
            
          <nav className="nav-2">
            <div className="menu-icon" onClick={ this.showMenu }><span className="fas fa-bars"></span></div>
            { !isHidden &&
            <Fragment>
              <div className={`overlay ${!isSlidingOut ? `fade-in` : `fade-out`}`} onClick={ this.hideMenu }></div>
              <ul className={`menu ${!isSlidingOut ? `slide-in` : `slide-out`}`}>
                <div className="menu-icon" onClick={ this.hideMenu }><span className="fas fa-times"></span></div>
                <Scroll type="id" element="_about" offset={-30}>ABOUT</Scroll>
                <Scroll type="id" element="_whatiuse" offset={-30}>WHAT I USE</Scroll>
                <Scroll type="id" element="_portfolio" offset={-20}>PORTFOLIO</Scroll>
                <Scroll type="id" element="_contact" offset={-30}>CONTACT</Scroll>
                <Scroll type="id" element="_footer">LINKS</Scroll>
              </ul>
            </Fragment> }
          </nav>
        </header>

        <div className="header-body" id="_header">
          <div className="bkgd">
            <div className="gradient"></div>
            <div className="square-pattern" style={{position:'fixed'}}></div>
            <div className="square-pattern-overlay"></div>
          </div>

          <div className="title">
            <div className="name">
              <div className="line"></div>
              <h2>JEMAINE BROWN</h2>
              <div className="line"></div>
            </div>
            <h4>WEB DEVELOPER &bull; DESIGNER</h4>
          </div>
          
          <div className="more">
            <Scroll type="id" element="_about" offset={-30}>
              <div className="more-arrow">
                <img className="down-arrow" src={downArrow} alt="arrow"/>
                <img className="down-arrow-glow" src={downArrowGlow} alt="arrow"/>
              </div>
            </Scroll>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default Header;


