import React from 'react';
import opLogo from '../../assets/images/openProcessing_logo.svg';

function Footer () {
  return (
    <footer id="_footer" className="footer">
      <div className="container">

        <ul className="links">
          <li>
            <a href="https://github.com/jemcafe" title="Github">
              <span className="fab fa-github"></span>
            </a>
          </li>
          <li>
            <a href="https://www.linkedin.com/in/jemaine-brown-65680bb2" title="Linkedin">
              <span className="fab fa-linkedin"></span>
            </a>
          </li>
          <li>
            <a href="https://www.behance.net/jemainebrod297" title="Behance">
              <span className="fab fa-behance"></span>
            </a>
          </li>
          <li>
            <a href="https://repl.it/@jemb" title="Repl.it (code samples)">
              <span className="fas fa-code"></span>
            </a>
          </li>
          <li>
            <a href="https://www.openprocessing.org/user/49432" title="Open Processing">
              <img src={opLogo} alt="open processing logo"/>
            </a>
          </li>
        </ul>

        <div className="copyright">&copy; Jemaine Brown</div>
      </div>
    </footer>
  );
}

export default Footer;


