import React from 'react';
import reduxLogo from '../../assets/images/redux_logo.png';
import ScrollAnimation from 'react-animate-on-scroll';

function WhatIUse () {
  return (
    <section id="_whatiuse" className="whatiuse">
      <div className="container panel">

        <div className="section-name" style={{margin: '0 0 30px 0'}}>
          <ScrollAnimation animateIn="fade-in-anim" animateOut="fade-out-anim" offset={100}>
            <div className="bar"></div>
          </ScrollAnimation>
          <ScrollAnimation animateIn="open-anim" animateOut="close-anim" offset={100}>
            <h3>WHAT&nbsp;I&nbsp;USE</h3>
          </ScrollAnimation>
          <ScrollAnimation animateIn="fade-in-anim" animateOut="fade-out-anim" offset={100}>
            <div className="bar"></div>
          </ScrollAnimation>
        </div>

        <ul>
          <li>
            <span className="stretch-in-anim"><i className="fab fa-js"></i></span>
            <p>JavaScript</p>
          </li>
          <li>
            <span className="grow-in-anim"><i className="fab fa-html5"></i></span>
            <p>HTML5</p>
          </li>
          <li>
            <span className="stretch-in-anim"><i className="fab fa-css3"></i></span>
            <p>CSS3</p>
          </li>
          <li>
            <span className="spin-in-anim spin-out-anim"><i className="fab fa-react"></i></span>
            <p>React</p>
          </li>
          <li>
            <span className="spin-in-anim"><i className="fab fa-node-js"></i></span>
            <p>Nodejs</p>
          </li>
          <li>
            <img className="grow-in-anim redux-img" src={reduxLogo} alt="redux" style={{margin:'0 auto',width: '55px'}}/>
            <p>Redux</p>
          </li>
          <li>
            <span className="devicon-postgresql-plain grow-in-anim"></span>
            <p>PostgreSQL</p>
          </li>
          <li>
            <span className="stretch-in-anim"><i className="fab fa-sass"></i></span>
            <p>Sass</p>
          </li>
          <li>
            <span className="stretch-in-anim"><i className="fab fa-git-square"></i></span>
            <p>Git</p>
          </li>
          <li>
            <span className="grow-in-anim"><i className="fab fa-npm"></i></span>
            <p>Npm</p>
          </li>
          {/* <li>
            <span className="grow-in-anim"><i className="fab fa-gulp"></i></span>
            <p>Gulp</p>
          </li> */}
          <li>
            <span className="devicon-photoshop-plain stretch-in-anim"></span>
            <p>Photoshop</p>
          </li>
          <li>
            <span className="devicon-visualstudio-plain grow-in-anim"></span>
            <p>VS Code</p>
          </li>
        </ul>

      </div>
    </section>
  )
}

export default WhatIUse;


